$color_lightpink: #ffb6c1;
$color_coral: #ff7f50;
$color_bg1: #ffb6c1;
$color_bg2: white;
$color_btn1: #ffb6c1;
$color_btn2: #ff7f50;

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

.uppercase{
  text-transform: uppercase;
}

.promo {
  .title {
    margin-top: 20px;
    font-family: 'Roboto', sans-serif;
  }

  .promo_btn {
    display: block;
    //margin: 73px auto; /*вертикаль, горизонталь*/
    width: 200px;
    height: 50px;
    border-radius: 100px;
    //box-shadow: inset 0 0 0 rgba(0, 0, 0, 0.23);
    //border: 4px solid rgba(0, 0, 0, 0.23);
    border: 0;
    background-image: linear-gradient(to top, $color_btn1 0%, $color_btn2 100%);
    text-align: center;
    color: white;
    //text-transform: uppercase;
    font-size: 21px;
    cursor: pointer;
    //outline: none;
  }
}

.container {
  position: relative;
  width: 1140px;
  margin: 0 auto; /*фокус, выравнивает контейнер по центру*/
}

.nav_menu {
  min-height: 80px;
  background-color: $color_lightpink;
}

.logo{

  //height: 150px;
  //width: 150px;
  &_text{
    color: red;
  }
  &_img{
    margin-top: 50px;
    height: 150px;
    width: 150px;
  }
}

.promo{
  background-color: $color_bg1;
}

.menu{
  background-color: $color_bg2;
}

.about{
  background-color: $color_bg1;
}

.work{
  background-color: $color_bg2;
}

.deliver{
  background-color: $color_bg1;
}

.feedback{
  background-color: $color_bg1;
}

.contacts{
  background-color: $color_bg2;
}